import Common from 'orion-common'
import Konva from 'konva'
import Utils from './utils'

class Model {
  constructor(config) {
    this.config = Common.Utils.lodash.merge({
      x: 0,
      y: 0,
      fill: '#ffffff',
      stroke: '#000000',
      strokeWidth: 1,
      type: 'node',
      dragDistance: 0,
      names: {
        node: true
      },
      location: {
        startLine: -1,
        startCol: -1,
        endLine: -1,
        endCol: -1
      },
      canClose: false,
      canJump: true,
      canMove: false,
      canExpand: false,
      acceptChild: false,
      acceptProperty: true
    }, config)

    this.config.uid = Common.Utils.common.uniqid() // 自动生成,
    this.config.names[this.config.type] = true
    this.isSelected = false

    this.root = new Konva.Group({
      x: this.config.x,
      y: this.config.y,
      draggable: false,
      dragDistance: this.config.dragDistance,
      name: Object.keys(this.config.names).join(' '),
      id: this.config.uid
    })

    // 创建背景
    this.background = this.createBackground()
    if (this.background) {
      this.root.add(this.background)
    }

    this.root.setAttr('@node', this)
    // 创建内容
    this.createBody()
  }

  /**
   * 创建背景
   */
  createBackground() {
    return new Konva.Rect({
      x: 0,
      y: 0,
      width: this.config.width,
      height: this.config.height,
      draggable: true,
      fill: this.config.fill,
      stroke: this.config.stroke,
      strokeWidth: this.config.strokeWidth
    })
  }

  /**
   * 创建节点内容
   */
  createBody() {
    this.components = {}
    this.body = new Konva.Group({
      x: 0,
      y: 0,
      name: 'model-body'
    })
    this.root.add(this.body)
  }

  /**
   * 获取父节点
   */
  parent() {
    let p = this.root.getParent()
    while (p) {
      if (p.hasName('element')) {
        break
      }
      p = p.getParent()
    }
    return p ? p.getAttr('@element') : null
  }

  /**
   * 
   * @param {*} parent
   */
  hasParent(parent) {
    let p = this.parent()
    while (p && p !== parent) {
      p = p.parent()
    }
    return p === parent
  }

  /**
   * 层级 root = 0
   */
  level() {
    let l = 0
    let p = this.parent()
    while (p) {
      l += 1
      p = p.parent()
    }
    return l
  }

  /**
   * 从父节点删除
   */
  remove() {}

  /**
   * 删除自己
   */
  destroy(self = true, notify = true) {
    this.root.destroy()
  }

  /**
   * 事件
   * @param {} evt 
   */
  deleteSelf (evt) {
    this.remove()
    this.destroy()
  }

  /**
   * 
   */
  adjust(option) {

  }

  /**
   * 
   * @param {*} flag 
   */
  selected(flag) {
    this.isSelected = flag
    if (this.background) {
      //this.background.strokeWidth(this.isSelected ? 2 : 1)
      this.background.fill(this.isSelected ? this.config.highlight : this.config.fill)
    }

  }
  /**
   *  刷新
   */
  refresh() {
    this.root.getLayer().draw()
  }

  /**
   * 
   */
  nodeType() {
    return this.config.type
  }

  /**
   * 判断类型
   * @param {*} type 
   */
  isType(type) {
    return this.root.hasName(type)
  }

  /**
   * 
   */
  toJson() {
    return {
      type: this.config.type,
      config: this.config
    }
  }

  /**
   * knova节点
   */
  knode() {
    return this.root
  }

  /**
   * 获取BTStage对象
   */
  stage() {
    let st = this.root.getStage()
    return st ? st.getAttr('@stage') : null
  }

  /**
   * 获取位置
   */
  position() {
    return this.root.position()
  }

  /**
   * 
   */
  size() {
    return {
      width: this.config.width,
      height: this.config.height
    }
  }

  /**
   * 
   */
  id() {
    return this.root.id()
  }

  /**
   * 
   * @param {*} show 
   */
  visible(show) {
    if (show == null) {
      return this.root.visible()
    }
    this.root.visible(show)
  }

    /**
   * 
   */
  canClose() {
    return this.config.canClose
  }

  /**
   * 
   */
  canJump() {
    return this.config.canJump
  }

  /**
   * 
   */
  canMove() {
    return this.config.canMove
  }

  /**
   * 
   */
  canExpand() {
    return this.config.canExpand
  }


  canAcceptChild() {
    return this.config.acceptChild
  }

  canAcceptProperty() {
    return this.config.acceptProperty
  }

  /**
   * 
   */
  clearContent(adjust) {}

  /**
   * 
   */
  dump() {
    return this.toJson()
  }

  /**
   * 
   */
  code() {
    return ''
  }

  /**
   * 
   */
  location () {
    return this.config.location
  }
}

export default Model