import Vue from 'vue'
import VueResource from 'vue-resource'
import VueI18n from 'vue-i18n'
import VueIntro from 'vue-introjs'
import VueCodemirror from 'vue-codemirror'

import IView from 'iview'

import 'iview/dist/styles/iview.css'
import 'font-awesome/css/font-awesome.min.css'
import 'intro.js/introjs.css'
import 'codemirror/lib/codemirror.css'

import App from './App.vue'
import router from './router'
import messages from './messages'

window.__vscodeExtMode__ = true
const vscodeCallbacks = {}
/**
 * 调用vscode原生api
 * @param data 可以是类似 {cmd: 'xxx', param1: 'xxx'}，也可以直接是 cmd 字符串
 * @param cb 可选的回调函数
 */
window.__callVscode__ = function (data, cb) {
  if (!window.__vscodeExtMode__) {
    return
  }

  if (typeof data === 'string') {
    data = { cmd: data }
  }
  if (cb) {
    // 时间戳加上5位随机数
    const cbid = Date.now() + '' + Math.round(Math.random() * 100000)
    vscodeCallbacks[cbid] = cb
    data.cbid = cbid
  }
  if (!window.__vscode__) {
    window.__vscode__ = acquireVsCodeApi()
  }
  window.__vscode__.postMessage(data)
}

window.addEventListener('message', event => {
  const message = event.data
  switch (message.cmd) {
    case 'vscodeCallback':
      if (vscodeCallbacks[message.cbid]) {
        vscodeCallbacks[message.cbid](message.data)
      }
      delete vscodeCallbacks[message.cbid]
      break
    default: break
  }
})

Vue.config.productionTip = false
Vue.use(VueI18n)
Vue.use(IView)
Vue.use(VueResource)
Vue.use(VueIntro)
Vue.use(VueCodemirror)

const i18n = new VueI18n({
  locale: 'en', // 语言标识
  messages
})

new Vue({
  router,
  i18n,
  render: h => h(App)
}).$mount('#app')
