import Common from 'orion-common'
import Konva from 'konva'
import Utils from './utils'
import Model from './model'

const _dfunc = function (config) {
  return `m 0,4 A 4,4 0 0,1 4,0 H ${ config.width - 4 } a 4,4 0 0,1 4,4 V ${config.height - 4} a 4,4 0 0,1 -4,4 H 4 a 4,4 0 0,1 -4,-4 z`
}

class Text extends Model {
  /**
   * 
   * @param {*} config
   * tagName  string
   */
  constructor (config) {

    let elemconfig = Common.Utils.lodash.merge({
      fill: '#fff',
      stroke: '#f00',
      strokeWidth: 1,
      highlight: Utils.text.highlight,
      dragDistance: Utils.dragDistance,
      type: 'text',
      names: {},
      tagName: 'text',
      tagCategory: 'wxml'
    }, config)

    elemconfig.acceptChild = false
    elemconfig.width = Math.max(config.width || 0, Utils.text.minWidth)
    elemconfig.height = Math.max(config.height || 0, Utils.text.minHeight)
 
    super(elemconfig)

    this.root.setAttr('@text', this)
 
    // 创建关闭按钮
    if (this.config.canClose) {
      this.components.closeBtn = Utils.widgets.closeButton({
        size: 10,
        uid: this.config.uid,
        action: () => {
          this.deleteSelf()
        }
      })
      this.root.add(this.components.closeBtn)
    }

    if (this.config.canJump) {
      this.components.jumpBtn = Utils.widgets.jumpButton({
        size: 10,
        uid: this.config.uid,
        action: () => {
          // 发送跳转动作消息
          let stage = this.stage()
          stage.notifyJump(this.config.location)
        }
      })
      this.root.add(this.components.jumpBtn)
    }

    this.adjust()
  }

   /**
   * 事件
   * @param {} evt 
   */
  deleteSelf (evt) {
    let stage = this.stage()
    if (stage && stage.isReadOnly()) {
      return
    }
    this.remove()
    this.destroy()
    stage.refresh()
    stage.snapshot()
  }

  /**
   * 创建背景
   */
  createBackground () {
    let bg = new Konva.Path({
      x: 0,
      y: 0,
      data: _dfunc(this.config),
      fill: this.config.fill,
      stroke: this.config.stroke,
      strokeWidth: this.config.strokeWidth,
      scaleX: 1,
      scaleY: 1
    })

    bg.hitFunc((context) => {
      context.beginPath()
      context.rect(0, 0, this.config.width, this.config.height)
      context.closePath()
      context.fillStrokeShape(bg)
    })

    return bg
  }

  /**
   * 创建节点内容
   */
  createBody () {
    super.createBody()

    // Text
    this.components.text = new Konva.Text({
      x: 8,
      y: 4,
      text: this.config.text,
      fontSize: Utils.text.fontSize,
      fontFamily: Utils.text.fontFamily,
      fill: '#000',
      listening: false
    })
    this.body.add(this.components.text)
  }


  /*
   * 从父节点删除
   */
  remove () {
    let parent = this.parent()
    if (parent) {
      parent.removeChild(this)
    }
  }

  /**
   * 
   * @param {*} child 
   */
  removeChild (child) {
    if (!child) {
      return
    }
    let index = this.children.indexOf(child)
    if (index >= 0) {
      this.children.splice(index, 1)
      child.knode().remove()
  
      this.adjust({
        bubble: true
      })
    }
  }

  /**
   * 
   */
  debrief () {
    let res = {
      node: this,
      parent: this.parent(),
      pos: -1
    }

    if (res.parent) {
      res.pos = res.parent.children.indexOf(this)
    }

    return res
  }

  /**
   * 调整宽度
   * @param {*} w 宽度
   */
  resizeWidth (w) {
    // 调整背景
    this.config.width = w
    this.background.data(_dfunc(this.config))

    // 调整关闭按钮
    if (this.components.closeBtn) {
      let bbox = this.components.closeBtn.getClientRect()
      // 调整close按钮
      this.components.closeBtn.position({
        x: this.config.width - bbox.width / 2,
        y: this.config.height / 2
      })
    }

    if (this.components.jumpBtn) {
      let bbox = this.components.jumpBtn.getClientRect()
      // 调整close按钮
      this.components.jumpBtn.position({
        x: this.config.width - bbox.width / 2,
        y: this.config.height / 2
      })
    }
  }

  /**
   * 调整宽度
   * @param {*} w 宽度
   */
  resizeHeight(h) {
    // 调整背景
    this.config.height = h
    this.background.data(_dfunc(this.config))

    // 调整关闭按钮
    if (this.components.closeBtn) {
      this.components.closeBtn.y(this.config.height / 2)
    }

    // 调整关闭按钮
    if (this.components.jumpBtn) {
      this.components.jumpBtn.y(this.config.height / 2)
    }
  }


  /**
   * 调整布局
   */
  adjust (option) {
    let opt = Object.assign({
      bubble: false,
      capture: false
    }, option)

    //  计算属性宽度
    let textWidth = this.components.text.width() + 32
    let needWidth = Math.max(Utils.text.minWidth, textWidth)
    this.resizeWidth(needWidth)

    let textHeight = this.components.text.height()
    let needHeight = Math.max(Utils.text.minHeight, textHeight + 8)
    this.resizeHeight(needHeight)

    if (opt.bubble) {
      let p = this.parent()
      if (p) {
        p.adjust({
          bubble: true
        })
      }
    }
  }

  /**
   * 
   */
  tagName () {
    return this.config.tagName
  }

  /**
   * 
   */
  code () {
    return this.config.text
  }

  /**
   * 
   * @param {*} text 
   */
  changeText (text) {
    this.config.text = text
    this.components.text.text(text)
    this.adjust({
      bubble: true
    })
  }

  /**
   * 
   * @param {*} adjust 
   */
  clearContent (adjust) {
    this.changeText('')
  }
}

export default Text
