import Common from 'orion-common'
import Konva from 'konva'
import Utils from './utils'
import Model from './model'
import Property from './property'

const _dfunc = function (config) {
  let headradius = config.headHeight / 2
  let d = `m 0,4 A 4,4 0 0,1 4,0 H ${ config.width - headradius } a ${headradius},${headradius} 0 0,1 0,${headradius*2} `
  if (config.expanded) {
    let footradius = 5
    d += `H 20 a 4,4 0 0,0 -4,4 v ${ config.height - config.headHeight - 18 } a 4,4 0 0,0 4,4 H ${ config.width - headradius } a ${footradius},${footradius} 0 0,1 0,${footradius*2} H 4 a 4,4 0 0,1 -4,-4 z`
  } else {
    d += `H 4 a 4,4 0 0,1 -4,-4 z`
  }
  return d
}

class Element extends Model {
  /**
   * 
   * @param {*} config
   * tagName: string
   * tagCategory: string
   */
  constructor(config) {

    let elemconfig = Common.Utils.lodash.merge({
      fill: 'pink',
      stroke: 'red',
      strokeWidth: 1,
      highlight: Utils.element.highlight,
      dragDistance: Utils.dragDistance,
      type: 'element',
      names: {},
      canExpand: true,
      expanded: false,
      acceptChild: true,
      headHeight: 20
    }, config)

    if (elemconfig.tagName === 'root') {
      elemconfig.fill = '#CDB5CD'
    } else if (elemconfig.tagName === 'template') {
      elemconfig.fill = '#33a3dc'
    }

    elemconfig.width = Math.max(config.width || 0, Utils.element.minWidth)
    elemconfig.height = Math.max(config.height || 0, Utils.element.minHeight)

    super(elemconfig)

    this.root.setAttr('@element', this)
    this.propertyMap = new Map()
    this.properties = []
    this.children = []

    // 创建关闭按钮
    if (this.config.canClose) {
      this.components.closeBtn = Utils.widgets.closeButton({
        size: 10,
        uid: this.config.uid,
        action: () => {
          this.deleteSelf()
        }
      })
      this.root.add(this.components.closeBtn)
    }

    // 创建跳转按钮
    if (this.config.canJump) {
      this.components.jumpBtn = Utils.widgets.jumpButton({
        size: 10,
        uid: this.config.uid,
        action: () => {
          // 发送跳转动作消息
          let stage = this.stage()
          stage.notifyJump(this.config.location)
        }
      })
      this.root.add(this.components.jumpBtn)
    }

    // 创建扩展按钮
    if (this.config.canExpand) {
      let exp = Utils.widgets.expandButton({
        size: 10,
        uid: this.config.uid,
        action: () => {
          this.toggleExpand()
          this.refresh()
        }
      })
      this.components.expandBtn = exp.button
      this.components.expandVline = exp.vline
      this.components.expandVline.visible(!this.config.expanded)
      this.root.add(this.components.expandBtn)
    }

    this.adjust()
  }

  /**
   * 事件
   * @param {} evt 
   */
  deleteSelf (evt) {
    let stage = this.stage()
    if (stage && stage.isReadOnly()) {
      return
    }
    this.remove()
    this.clearContent()
    this.destroy()
    stage.refresh(true)
    stage.snapshot()
  }

  /**
   * 创建背景
   */
  createBackground() {
    let bg = new Konva.Path({
      x: 0,
      y: 0,
      data: _dfunc(this.config),
      fill: this.config.fill,
      stroke: this.config.stroke,
      strokeWidth: this.config.strokeWidth,
      scaleX: 1,
      scaleY: 1
    })

    bg.hitFunc((context) => {
      let size = this.size()
      context.beginPath()
      context.rect(0, 0, size.width, size.height)
      context.closePath()
      context.fillStrokeShape(bg)
    })

    return bg
  }

  /**
   * 创建节点内容
   */
  createBody() {
    super.createBody()
    //Tag
    this.components.name = new Konva.Text({
      x: 20,
      y: 2,
      text: this.config.tagName,
      fontSize: 14,
      fontFamily: 'Calibri',
      fontStyle: 'bold',
      fill: '#000',
      listening: false
    })
    this.body.add(this.components.name)
    this.propOffset = 40 + this.components.name.width()
  }

  /**
   * 
   * @param {*} flag 
   */
  expand(flag) {
    this.config.expanded = flag
    this.components.expandVline.visible(!this.config.expanded)
    this.background.data(_dfunc(this.config))
    for (let child of this.children) {
      child.knode().visible(this.config.expanded)
    }
    this.adjust({
      bubble: true
    })
  }

  /**
   * 
   */
  toggleExpand() {
    this.expand(!this.config.expanded)
  }

  /**
   * 
   * @param {*} boardcast 
   */
  expandChildren(flag, boardcast = false) {
    for (let child of this.children) {
      if (child.nodeType() === 'element') {
        if (boardcast) {
          child.expandChildren(flag, boardcast)
        }
        child.expand(flag)
      }
    }
  }

  /**
   * 
   * @param {*} prop 
   */
  addProperty(prop) {
    let property = new Property(Object.assign({
      x: this.propOffset,
      y: 2
    }, prop))
    this.body.add(property.knode())
    this.properties.push(property)
    this.propertyMap.set(property.key(), property)
    return property
  }

  addChild(child) {
    if (!child) {
      return
    }
    child.knode().x(16)
    child.knode().visible(this.config.expanded)
    this.children.push(child)
    this.body.add(child.knode())
    this.adjust()
  }

  /*
   * 从父节点删除
   */
  remove() {
    let parent = this.parent()
    if (parent) {
      parent.removeChild(this)
    }
  }

  /**
   * 
   * @param {*} child 
   */
  removeChild(child) {
    if (!child) {
      return
    }
    let index = this.children.indexOf(child)
    if (index >= 0) {
      this.children.splice(index, 1)
      child.knode().remove()
    }
    this.adjust()
  }

  insertChild(child, index) {
    child.knode().x(16)
    child.knode().visible(this.config.expanded)
    this.body.add(child.knode())
    if (index < 0) {
      this.children.push(child)
    } else {
      this.children.splice(index, 0, child)
    }
    this.adjust()
  }

  /**
   * 
   * @param {*} point 
   */
  placeholderChild(point) {
    let width = this.config.width
    let height = this.config.height
    let pos = this.root.getAbsolutePosition()
    let zoom = this.stage().zoom
    let zone = {
      top: pos.y,
      bottom: pos.y + height * zoom,
      left: pos.x,
      right: pos.x + width * zoom
    }

    if (point.y < zone.top || point.y > zone.bottom) {
      return -1
    }

    let index = -1
    // 如果没有子节点
    if (this.children.length === 0) {
      index = 0
    } else {
      let start = zone.top + this.config.headHeight * zoom
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        start += child.size().height * zoom
        if (point.y < start) {
          index = i
          break
        }
      }
    }

    return index
  }

  /**
   * 
   * @param {*} a 
   * @param {*} b 
   */
  swapChild(a, b) {
    if (a < 0) {
      a = this.children.length - 1
    }

    if (b < 0) {
      b = this.children.length - 1
    }

    if (a === b) {
      return
    }
    let temp = this.children[a]
    this.children[a] = this.children[b]
    this.children[b] = temp
    this.adjust()
  }

  /**
   * 
   * @param {*} adjust 
   */
  clearProperty () {
    for (let prop of this.properties) {
      prop.destroy()
    }
    this.properties = []
    this.propertyMap.clear()
  }

  /**
   * 
   * @param {*} key 
   */
  removeProperty(key) {
    if (this.propertyMap.has(key)) {
      let prop = this.propertyMap.get(key)
      prop.knode().remove()
      this.propertyMap.delete(key)
      let index = this.properties.indexOf(prop)
      this.properties.splice(index, 1)
      this.adjust({
        bubble: true
      })
    }
  }

  insertProperty(prop, index) {
    // 检查是否有重复属性
    if (this.propertyMap.has(prop.config.key)) {
      let p = this.propertyMap.get(prop.config.key)
      let idx = this.properties.indexOf(p)
      this.swapProperty(index, idx)
      p.value(prop.config.value)
      prop.destroy()
      this.adjust({
        bubble: true
      })
      return
    }

    this.propertyMap.set(prop.config.key, prop)
    this.body.add(prop.knode())
    if (index < 0) {
      this.properties.push(prop)
    } else {
      this.properties.splice(index, 0, prop)
    }
    this.adjust({
      bubble: true
    })
  }

  /**
   * 判断插入位置
   * @param point // 画布点坐标
   * @return int 返回插入索引 -1 表示不能插入
   */
  placeholderProperty(point) {
    let width = this.config.width
    let pos = this.root.getAbsolutePosition()
    let zoom = this.stage().zoom
    let zone = {
      top: pos.y,
      bottom: pos.y + this.config.headHeight * zoom,
      left: pos.x,
      right: pos.x + width * zoom
    }

    if (point.x < zone.left || point.x > zone.right || point.y < zone.top || point.y > zone.bottom) {
      return -1
    }

    let index = -1
    // 如果没有子节点
    if (this.properties.length === 0) {
      index = 0
    } else {
      let offsetx = (point.x - zone.left) / zoom
      if (offsetx <= this.propOffset) {
        index = 0
      }
      // let offsety = (point.y - zone.top) / zoom
      // for (let i = 0; i < this.properties.length; i++) {
      //   let prop = this.properties[i]
      //   let pos = prop.position()
      //   if (offsetx >= pos.x &&
      //     offsetx <= pos.x + prop.config.width &&
      //     offsety >= pos.y &&
      //     offsety <= pos.y + prop.config.height) {
      //     index = i
      //     break
      //   }
      // }
    }
    return index
  }

  /**
   * 
   * @param {*} a 
   * @param {*} b 
   */
  swapProperty(a, b) {
    if (a < 0) {
      a = this.properties.length - 1
    }

    if (b < 0) {
      b = this.properties.length - 1
    }

    if (a === b) {
      return
    }

    let temp = this.properties[a]
    this.properties[a] = this.properties[b]
    this.properties[b] = temp
    this.adjust({
      bubble: true
    })
  }

  /**
   * 
   */
  debrief() {
    let res = {
      node: this,
      parent: this.parent(),
      pos: -1
    }

    if (res.parent) {
      res.pos = res.parent.children.indexOf(this)
    }

    return res
  }

  /**
   * 调整宽度
   * @param {*} w 宽度
   */
  resizeWidth(w) {
    // 调整背景
    this.config.width = w
    this.background.data(_dfunc(this.config))

    // 调整属性
    let start = this.propOffset
    let offsety = 2
    for (let prop of this.properties) {
      if (start + prop.config.width + 8 >= this.config.width) {
        start = this.propOffset
        offsety += 20
      }
      prop.knode().x(start)
      prop.knode().y(offsety)
      start += prop.config.width + 8
    }

    // 调整关闭按钮
    if (this.components.closeBtn) {
      let width = this.config.width
      let bbox = this.components.closeBtn.getClientRect()
      // 调整close按钮
      this.components.closeBtn.position({
        x: width - bbox.width / 2,
        y: this.config.headHeight / 2
      })
    }

    if (this.components.jumpBtn) {
      let width = this.config.width
      let bbox = this.components.jumpBtn.getClientRect()
      // 调整close按钮
      this.components.jumpBtn.position({
        x: width - bbox.width / 2,
        y: this.config.headHeight / 2
      })
    }
  }

  /**
   * 调整宽度
   * @param {*} w 宽度
   */
  resizeHeight(h) {
    // 调整背景
    this.config.height = h
    this.background.data(_dfunc(this.config))

    // 调整属性
    let start = this.config.headHeight
    for (let child of this.children) {
      child.knode().y(start)
      start += child.size().height
    }
  }

  /**
   * 调整布局
   */
  adjust(option) {
    let opt = Object.assign({
      bubble: false,
      capture: false
    }, option)

    //  计算属性宽度
    let headHeight = 20
    let propWidth = this.propOffset + 20
    let maxPropWidth = Utils.element.minWidth

    for (let prop of this.properties) {
      propWidth += prop.config.width + 8
      if (propWidth >= Utils.element.maxWidth) {
        propWidth -= (prop.config.width + 8)
        maxPropWidth = Math.max(maxPropWidth, propWidth)
        propWidth = this.propOffset + 20
        propWidth += prop.config.width + 8
        headHeight += 20
      }
    }

    propWidth = Math.max(maxPropWidth, propWidth)
    this.config.headHeight = headHeight

    // 计算子节点高度
    let childHeight = 0
    if (this.config.expanded) {
      for (let child of this.children) {
        if (opt.capture) {
          child.adjust({
            capture: true
          })
        }
        childHeight += child.size().height
      }
      childHeight = Math.max(20, childHeight)
    }

    let needWidth = Math.max(Utils.element.minWidth, propWidth)
    this.resizeWidth(needWidth)
    let needHeight = Math.max(Utils.element.minHeight, childHeight + 10 + this.config.headHeight)
    this.resizeHeight(needHeight)

    if (opt.bubble) {
      let p = this.parent()
      if (p) {
        p.adjust({
          bubble: true
        })
      }
    }
  }

  /**
   * 
   */
  tagName () {
    return this.config.tagName
  }

  /**
   * 
   */
  tagCategory () {
    return this.config.tagCategory
  }

  /**
   * 
   */
  isExpanded () {
    return this.config.expanded
  }

  /**
   * 
   */
  toJson() {
    let json = {
      type: this.config.type,
      config: this.config,
      properties: [],
      children: []
    }
    for (let prop of this.properties) {
      json.properties.push(prop.toJson())
    }
    for (let child of this.children) {
      json.children.push(child.toJson())
    }

    return json
  }

  /**
   * 
   */
  size() {
    return {
      width: this.config.width,
      height: this.config.expanded ? this.config.height : this.config.headHeight
    }
  }

  /**
   * 
   */
  clearContent (adjust) {
    this.clearProperty()

    for (let child of this.children) {
      child.clearContent()
      child.destroy()
    }
    this.children = []

    if (adjust) {
      this.adjust(adjust)
    }
  }

  /**
   * 
   */
  code() {
    let text = `<${this.config.tagName}`
    for (let prop of this.properties) {
      text += ` ${prop.code()}`
    }
    text += '>'

    for (let child of this.children) {
      text += child.code()
    }

    text += `</${this.config.tagName}>`
    return text
  }

  /**
   * 查找模型
   * @param {*} opt 
   */
  search(opt) {
    if (!opt.key || opt.key.length === 0) {
      return []
    }

    let res = []
    if (this.config.tagName === 'root') {
    } else {
      if (opt.hasElem && this.config.tagName.toLowerCase().indexOf(opt.key) >= 0) {
        res.push(this)
      }

      if (opt.hasProp) {
        for (let prop of this.properties) {
          if (prop.key().toLowerCase().indexOf(opt.key) >= 0 || prop.value().toLowerCase().indexOf(opt.key) >= 0) {
            res.push(prop)
          }
        }
      }
    }

    // 递归搜索
    for (let child of this.children) {
      if (child.nodeType() === 'text') {
        if (opt.hasText &&child.code().toLowerCase().indexOf(opt.key) >= 0) {
          res.push(child)
        }
      } else if (child.nodeType() === 'comment') {
        if (opt.hasComment && child.code(false).toLowerCase().indexOf(opt.key) >= 0) {
          res.push(child)
        }
      } else {
        Array.prototype.push.apply(res, child.search(opt))
      }
    }
    return res
  }
}

export default Element