import Common from 'orion-common'
import Konva from 'konva'
import Utils from './utils'
import Model from './model'

const _dfunc = function (config) {
  let radius = config.height / 2
  return `m 0,4 a 4,4 0 0,1 4,-4 H ${ config.width - radius } a ${radius},${radius} 0 0,1 0,${config.height} H 4 a 4,4 0 0,1 -4,-4 z`
}

class Property extends Model {
  constructor (config) {
    let propconfig = Common.Utils.lodash.merge({
      x: 0,
      y: 0,
      type: 'property',
      highlight: Utils.property.highlight,
      names: {},
      key: '',
      value: '',
      event: false
    }, config)

    propconfig.key = propconfig.name
    propconfig.width = Math.max(config.width || 0, Utils.property.minWidth)
    propconfig.height = Math.max(config.height || 0, Utils.property.minHeight)
 
    super(propconfig)
    this.root.setAttr('@property', this)

    // 创建关闭按钮
    if (this.config.canClose) {
      this.components.closeBtn = Utils.widgets.closeButton({
        uid: this.config.uid,
        action: () => {
          this.deleteSelf()
        }
      })
      this.root.add(this.components.closeBtn)
    }

    if (this.config.canJump) {
      this.components.jumpBtn = Utils.widgets.jumpButton({
        uid: this.config.uid,
        action: () => {
          // 发送跳转动作消息
          let stage = this.stage()
          stage.notifyJump(this.config.location)
        }
      })
      this.root.add(this.components.jumpBtn)
    }

    this.adjust()
  }

  /**
   * 事件
   * @param {} evt 
   */
  deleteSelf (evt) {
    let stage = this.stage()
    if (stage && stage.isReadOnly()) {
      return
    }
    this.remove()
    this.destroy()
    stage.refresh()
    stage.snapshot()
  }

  /**
   * 创建背景
   */
  createBackground () {
    let bg = new Konva.Path({
      x: 0,
      y: 0,
      data: _dfunc(this.config),
      fill: this.config.fill,
      stroke: this.config.stroke,
      strokeWidth: this.config.strokeWidth,
      scaleX: 1,
      scaleY: 1
    })

    bg.hitFunc((context) => {
      context.beginPath()
      context.rect(0, 0, this.config.width, this.config.height)
      context.closePath()
      context.fillStrokeShape(bg)
    })

    return bg
  }

  /**
   * 创建节点内容
   */
  createBody () {
    super.createBody()
    let offsety = (this.config.height - Utils.property.fontSize) / 2
    // Key
    this.components.key = new Konva.Text({
      x: 4,
      y: offsety,
      text: this.config.name,
      fontSize: Utils.property.fontSize,
      fontFamily: 'Calibri',
      fill: '#fff',
      listening: false
    })

    let keybgColor = '#9400D3'
    if (this.isEvent()) {
      keybgColor = '#006400'
    } else if (this.isDirective()) {
      keybgColor = '#1A1A1A'
    }

    this.components.keybg = new Konva.Rect({
      x: 0,
      y: 0,
      width: this.components.key.width() + 8,
      height: this.config.height,
      fill: keybgColor,
      stroke: this.config.stroke,
      strokeWidth: this.config.strokeWidth,
      cornerRadius: 4
    })

    this.body.add(this.components.keybg)
    this.body.add(this.components.key)

    // Value
    this.components.value = new Konva.Text({
      x: this.components.key.width() + 12,
      y: offsety,
      text: this.processValue(this.config.value),
      fontSize: Utils.property.fontSize,
      fontFamily: 'Calibri',
      fill: '#000',
      listening: false
    })
    this.body.add(this.components.value)
  }

  /*
   * 从父节点删除
   */
  remove () {
    let parent = this.parent()
    if (parent) {
      parent.removeProperty(this.config.key)
    }
  }

  /**
   * 
   */
  debrief () {
    let res = {
      name: this.config.name,
      key: this.config.key,
      node: this,
      parent: this.parent(),
      pos: -1
    }

    if (res.parent) {
      res.pos = res.parent.properties.indexOf(this)
    }

    return res
  }


  /**
   * 调整宽度
   * @param {*} w 宽度
   */
  resizeWidth (w) {
    // 调整背景
    if (w) {
      // 调整背景
      this.config.width = w
      this.background.data(_dfunc(this.config))
    }

    // 调整关闭按钮
    if (this.components.closeBtn) {
      let width = this.config.width
      let bbox = this.components.closeBtn.getClientRect()
      // 调整close按钮
      this.components.closeBtn.position({
        x: width - bbox.width / 2,
        y: this.config.height / 2
      })
    }

    if (this.components.jumpBtn) {
      let width = this.config.width
      let bbox = this.components.jumpBtn.getClientRect()
      // 调整close按钮
      this.components.jumpBtn.position({
        x: width - bbox.width / 2,
        y: this.config.height / 2
      })
    }
  }

  /**
   * 调整布局
   */
  adjust (option) {
    let contentWidth = this.components.key.width() + 12 + this.components.value.width() + this.config.height
    let needWidth = Math.max(Utils.property.minWidth, contentWidth)
    this.resizeWidth(needWidth)
  }

  isEvent () {
    return this.config.event
  }

  isDirective () {
    return this.config.name.indexOf('wx:') === 0
  }

  key () {
    return this.config.key
  }

  processValue(v) {
    let str = ''
    if (v.length > 60) {
      str = v.slice(0, 60) + '...'
    } else {
      str = v.slice()
    }
    return str
  }

  value (v) {
    if (v) {
      this.config.value = v
      this.components.value.text(this.processValue(v))
      this.adjust({
        bubble: true
      })
      return
    }
    return this.config.value
  }
  /**
   * 
   */
  code () {
    return `${this.config.name}="${this.config.value}"`
  }
}

export default Property
