import Common from 'orion-common'
import Konva from 'konva'
import Utils from './utils'
import Model from './model'

const _dfunc = function (config) {
  let radius = config.height / 2
  return `m 0,4 A 4,4 0 0,1 4,0 H ${ config.width - radius } a ${radius},${radius} 0 0,1 0,${ radius * 2 } H 4 a 4,4 0 0,1 -4,-4 z`
}

class Holder extends Model {
  /**
   * 
   * @param {*} config
   * tagName  string
   */
  constructor (config) {

    let elemconfig = Common.Utils.lodash.merge({
      fill: '#ccc',
      stroke: 'black',
      strokeWidth: 1,
      type: 'holder',
      names: {},
      canClose: false,
      canMove: false,
      acceptChild: false,
      acceptProperty: false,
      key: '**holder**'
    }, config)

    elemconfig.width = Utils.holder.minWidth
    elemconfig.height = Utils.holder.minHeight
    super(elemconfig)

    this.root.setAttr('@holder', this)
    this.setPrototype(config.proto)
  }

  /**
   * 创建背景
   */
  createBackground () {
    let bg = new Konva.Path({
      x: 0,
      y: 0,
      data: _dfunc(this.config),
      fill: this.config.fill,
      stroke: this.config.stroke,
      strokeWidth: this.config.strokeWidth,
      scaleX: 1,
      scaleY: 1
    })

    bg.hitFunc((context) => {
      let size = this.size()
      context.beginPath()
      context.rect(0, 0, size.width, size.height)
      context.closePath()
      context.fillStrokeShape(bg)
    })

    return bg
  }

  /**
   * 创建节点内容
   */
  createBody () {
  }

  setPrototype (element) {
    this.config.proto = element
    if (this.config.proto) {
      let protoSize = this.config.proto.size()
      this.config.width = protoSize.width
      this.config.height = protoSize.height
      this.root.position(this.config.proto.position())
    }
    this.adjust()
  }

  /**
   * 调整布局
   */
  adjust (option) {
    this.background.data(_dfunc(this.config))
  }
}

export default Holder
